#shootingTestHome {
  position: absolute;
  left: 50%;
  top: 50%;

  transform: translate(-50%, -50%);

  display: flex;
  justify-content: center;
  align-items: center;

  font-family: "GothamX";
}

#shootingTestHome > img {
  position: absolute;
  left: 0;
  bottom: 0;

  height: 95%;
}

#shootingTestHome > section {
  max-width: 63.8889vh;

  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 2.7778vh;

  color: rgba(255, 255, 255, 0.3);
  text-align: center;
}

#shootingTestHome > section h1 {
  text-transform: uppercase;

  color: white;
  font-family: "GothamB";
  letter-spacing: 0.0926vh;
  font-size: 2.2454vh;
  padding: 0.6944vh 2.1296vh 0.9259vh;

  border-radius: 0.3704vh;
  border: 0.125vh solid #E02E2E;

  background: linear-gradient(135deg, #E02E2E -50%, #4D1414 120%);
}

#shootingTestHome > section .welcome {
  font-size: 1.4815vh;
  line-height: 110%;
}

#shootingTestHome > section > article {
  background: linear-gradient(150deg, rgba(255, 255, 255, 0.06), rgba(255, 255, 255, 0.02));

  padding: 3.5185vh 3.6111vh;

  border-radius: 0.9259vh;
  border: 0.0926vh solid rgba(255, 255, 255, 0.12);

  font-size: 1.6667vh;
  line-height: 115%;

  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 3.1482vh;
}

article .testPrice {
  background: linear-gradient(150deg, rgba(255, 255, 255, 0.06), rgba(255, 255, 255, 0.02));

  border-radius: 0.9259vh;
  border: 0.0926vh solid rgba(255, 255, 255, 0.12);

  padding: 1.4815vh 2.4074vh;

  width: fit-content;

  font-family: "GothamX";
  font-weight: bold;
  font-size: 1.6667vh;
}