#shootingTestResult {
  position: absolute;
  left: 50%;
  top: 50%;

  transform: translate(-50%, -50%);

  display: flex;
  justify-content: center;
  align-items: center;

  font-family: "GothamX";
}

#shootingTestResult > section {
  width: 63.8889vh;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 2.7778vh;

  color: rgba(255, 255, 255, 0.3);
  text-align: center;

}

#shootingTestResult > section h1 {
  text-transform: uppercase;

  color: white;
  font-family: "GothamB";
  letter-spacing: 0.0926vh;
  font-size: 2.2454vh;
  padding: 0.6944vh 2.1296vh 0.9259vh;

  border-radius: 0.3704vh;
  border: 0.125vh solid #E02E2E;

  background: linear-gradient(135deg, #E02E2E -50%, #4D1414 120%);
}

#shootingTestQuestions > section > p {
  font-size: 1.6667vh;
}

#shootingTestResult > section article {
  display: flex;
  flex-direction: column;
  gap: 1.2963vh;

  width: 100%;
}

#shootingTestResult > section article div {
  background: rgba(255, 255, 255, 0.03);

  border-radius: 0.9259vh;
  border: 0.0926vh solid rgba(255, 255, 255, 0.12);
  
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.2778vh 0.7407vh 0.2778vh 1.8519vh;
  
  text-transform: uppercase;
  font-family: "Gotham";
  letter-spacing: 0.0926vh;
}

#shootingTestResult > section article div p {
  font-size: 1.6667vh;
  color: rgba(255, 255, 255, 0.8);
}

#shootingTestResult > section article div aside {
  color: white;
  font-family: "GothamExtraBold";
  text-align: center;
  font-size: 2.037vh;
  line-height: 100%;

  padding-block: 0.8333vh 1.0648vh;

  width: 10.1852vh;

  border-radius: 0.3704vh;
  border: 0.125vh solid #E02E2E;

  background: linear-gradient(135deg, #E02E2E -50%, #4D1414 120%);
}