#shootingTestHotTrack {
  width: 100vw;
  height: 100vh;
  position: relative;

  display: flex;
  align-items: center;

  font-family: "GothamX";
}

#shootingTestHotTrack::before {
  content: "";

  position: absolute;
  inset: 0;
  z-index: -1;

  opacity: 1;

  background: linear-gradient(90deg, #151619, rgba(21, 22, 25, 0));

  transition: all .2s;
}

#shootingTestHotTrack > section {
  max-width: 40.1852vh;

  display: flex;
  flex-direction: column;
  gap: 2.7778vh;

  margin-left: 4.3519vh;
}

#shootingTestHotTrack > section h1 {
  text-transform: uppercase;

  width: fit-content;

  color: white;
  font-family: "GothamB";
  letter-spacing: 0.0926vh;
  font-size: 2.2454vh;
  padding: 0.6944vh 2.1296vh 0.9259vh;

  border-radius: 0.3704vh;
  border: 0.125vh solid #E02E2E;

  background: linear-gradient(135deg, #E02E2E -50%, #4D1414 120%);
}

#shootingTestHotTrack > section p {
  font-size: 1.6667vh;
  color: rgba(255, 255, 255, 0.3);
}

#shootingTestHotTrack .targetsGrid {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-auto-rows: auto;
  gap: 0.9259vh;
}

#shootingTestHotTrack .targetsGrid .targetItem {
  width: 5.3704vh;
  height: 5.3704vh;

  position: relative;
  overflow: hidden;

  display: flex;
  align-items: center;
  justify-content: center;

  background: rgba(255, 255, 255, 0.06);
  
  border-radius: 0.9259vh;
  border: 0.0926vh solid rgba(255, 255, 255, 0.12);

  transition: all .2s;
}

#shootingTestHotTrack .targetsGrid .targetItem.targetHit {
  background: linear-gradient(135deg, #E02E2E -50%, #4D1414 120%);
  border: 0.125vh solid #E02E2E;
}

#shootingTestHotTrack .targetsGrid .targetItem.targetHit img {
  opacity: 1 !important;
}

#shootingTestHotTrack .targetsGrid .targetItem img {
  width: 2.4074vh;
  opacity: .1;

  transition: all .2s;
}

#shootingTestHotTrack > aside {
  position: absolute;
  bottom: 6.7593vh;
  left: 4.3519vh;

  display: flex;
  flex-direction: column;
  gap: 1.8519vh;
}

#shootingTestHotTrack > aside div {
  display: flex;
  gap: 1.4815vh;
  align-items: center;

  color: rgba(255, 255, 255, 0.3);
}

#shootingTestHotTrack > aside div .styledLetter {
  width: 5.3704vh;
  height: 5.3704vh;
  font-size: 2.4074vh;

  text-transform: uppercase;
  color: white;
  font-family: "Gotham";

  border-radius: 0.9259vh;
  border: 0.125vh solid #E02E2E;

  background: linear-gradient(135deg, #E02E2E -50%, #4D1414 120%);

  display: flex;
  justify-content: center;
  align-items: center;
}

#counter {
  font-size: 5.5556vh;
  font-family: "Gotham";
  color: white;

  width: 12.963vh;
  height: 12.963vh;

  border-radius: 100%;
  border: 0.1481vh solid rgba(234, 239, 243, 0.2);

  background: rgba(21, 22, 25, 0.95);
  display: flex;
  align-items: center;
  justify-content: center;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  animation: counter .2s;
}

#shootingTestHotTrack.testRunning::before,
#shootingTestHotTrack.testRunning > section h1,
#shootingTestHotTrack.testRunning > section p,
#shootingTestHotTrack.testRunning aside,
#shootingTestHotTrack.counterRunning > section,
#shootingTestHotTrack.counterRunning > aside {
  opacity: 0;
} 