#shootingTestQuestions {
  position: absolute;
  left: 50%;
  top: 50%;

  transform: translate(-50%, -50%);

  display: flex;
  justify-content: center;
  align-items: center;

  font-family: "GothamX";
}

#shootingTestQuestions > section {
  max-width: 63.8889vh;

  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 2.7778vh;

  color: rgba(255, 255, 255, 0.3);
  text-align: center;
}

#shootingTestQuestions > section > p {
  font-size: 1.6667vh;
}

#shootingTestQuestions > section h1 {
  text-transform: uppercase;

  color: white;
  font-family: "GothamB";
  letter-spacing: 0.0926vh;
  font-size: 2.2454vh;
  padding: 0.6944vh 2.1296vh 0.9259vh;

  border-radius: 0.3704vh;
  border: 0.125vh solid #E02E2E;

  background: linear-gradient(135deg, #E02E2E -50%, #4D1414 120%);
}

#shootingTestQuestions .answersContainer {
  display: flex;
  flex-direction: column;
  gap: 1.1111vh;

  width: 100%;
}

#shootingTestQuestions .answerBtn {
  color: rgba(255, 255, 255, 0.2);
  font-family: "Gotham";
  font-size: 1.8519vh;

  background: rgba(255, 255, 255, 0.03);

  border-radius: 0.9259vh;
  border: 0.0926vh solid rgba(255, 255, 255, 0.12);

  padding: 1.3889vh 0;

  width: 100%;

  position: relative;
  overflow: hidden;
  z-index: 1;

  transition: .2s all;
}

#shootingTestQuestions .answerBtn:hover {
  background: rgba(255, 255, 255, 0.06);
  color: rgba(255, 255, 255, 0.4);
  border: 0.0926vh solid rgba(255, 255, 255, 0.2);
}

#shootingTestQuestions .answerBtn::before {
  content: "";

  position: absolute;
  z-index: -1;
  inset: 0;

  background: linear-gradient(135deg, #ffffff, rgba(255, 255, 255, 0.35));

  opacity: 0;

  transition: .2s all;
}

#shootingTestQuestions .answerBtn.active {
  color: #171717;

  border-color: transparent;
}

#shootingTestQuestions .answerBtn.active::before {
  opacity: 1;
}