#shootingTestSeekLicense {
  position: absolute;
  left: 50%;
  top: 50%;

  transform: translate(-50%, -50%);

  width: 49.3519vh;

  padding: 1.2963vh;
  padding-bottom: 2.2222vh;

  background: rgba(21, 22, 25, .95);

  border-radius: 2.2222vh;
  border: 0.1546vh solid rgba(234, 239, 243, 0.2);

  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 1.2963vh;

  font-family: "Gotham";

  animation: arrive .2s forwards;

}

#shootingTestSeekLicense .seekLicenseInputContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;

  background: rgba(255, 255, 255, 0.06);

  width: 100%;

  padding-block: 0.6481vh;
  padding-inline: 0 0.8333vh;

  border-radius: 0.9259vh;
  border: 0.0926vh solid rgba(255, 255, 255, 0.12);
}

#shootingTestSeekLicense .seekLicenseInputContainer label {
  margin-inline: 1.8519vh;
  position: relative;
}

#shootingTestSeekLicense .seekLicenseInputContainer label::after {
  content: "";

  position: absolute;
  right: -1.8519vh;
  top: 50%;
  transform: translateY(-50%);

  width: 0.1852vh;
  height: 1.4815vh;

  background: rgba(255, 255, 255, 0.06);
}

#shootingTestSeekLicense .seekLicenseInputContainer label img {
  min-width: 1.8519vh;
  width: 1.8519vh;
}

#shootingTestSeekLicense input {
  background: none;
  border: none;
  outline: none;

  width: 100%;

  color: rgba(255, 255, 255, 0.8);
  font-size: 1.4815vh;
  font-family: "Gotham";

  padding-inline: 1.8519vh 0.9259vh;
}

#shootingTestSeekLicense input::placeholder {
  color: rgba(255, 255, 255, 0.14);
}

#shootingTestSeekLicense > section {
  width: 100%;

  display: flex;
  justify-content: center;
}

#shootingTestSeekLicense > section > p {
  text-align: center;
  color: rgba(255, 255, 255, 0.2);
  font-family: "GothamX";
  font-size: 1.4815vh;
  max-width: 37.5vh;
}

#shootingTestSeekLicense .searchedUser {
  display: flex;
  justify-content: space-between;

  background: rgba(255, 255, 255, 0.06);

  width: 100%;

  padding-block: 1.6111vh;
  padding-inline: 2.037vh 2.2222vh;

  border-radius: 0.9259vh;
  border: 0.0926vh solid rgba(255, 255, 255, 0.12);

  color: white;
  font-weight: 500;
}

#shootingTestSeekLicense .searchedUser img {
  width: 100%;
}

#shootingTestSeekLicense .searchedUser .imgContainer {
  position: relative;
  margin-right: 2.037vh;

  display: flex;
  align-items: center;
  justify-content: center;
}

#shootingTestSeekLicense .searchedUser .imgContainer::after {
  content: "";

  position: absolute;
  right: -1.8519vh;
  top: 50%;
  transform: translateY(-50%);

  width: 0.1852vh;
  height: 1.4815vh;

  background: rgba(255, 255, 255, 0.06);
}

#shootingTestSeekLicense .searchedUser aside {
  display: flex;
  align-items: center;
  justify-content: space-between;

  width: 100%;

  padding-left: 1.8519vh;

  font-size: 1.4815vh;
}