.btn {
  text-transform: uppercase;
  color: white;
  font-size: 1.6667vh;
  letter-spacing: 0.0926vh;
  font-family: "Gotham";

  padding: 0 2.1296vh;
  height: 4.9074vh;

  border-radius: 0.3704vh;
  border: 0.125vh solid #E02E2E;

  background: linear-gradient(135deg, #E02E2E -50%, #4D1414 120%);

  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2.1296vh;
  
  position: relative;

  overflow: hidden;
  z-index: 1;
}

.btn img {
  height: 1.1111vh;
}

.btn.closeBtn {
  width: 4.0741vh;
  height: 4.0741vh;

  padding: 0;

  position: absolute ;
  right: 5.5556vh;
  top: 5.5556vh;
}

.btn.closeBtn img {
  width: 2.5vh;
  height: 2.5vh;
}

.btn.small {
  font-size: 1.4815vh;
  height: 3.9815vh;
  padding-inline: 2.7778vh;
}

.btn.medium {
  font-size: 1.4815vh;
  height: 4.3519vh;
  padding-inline: 2.7778vh;
}

.btn::before {
  content: '';
  
  position: absolute;
  top: 0;
  left: -50%;

  width: 200%;
  height: 100%;

  background: linear-gradient(135deg, #E02E2E, #4D1414 60%);

  transition: left .3s ease;
  z-index: -1;
}

.btn:hover::before {
  left: 0;
}