* {
  margin: 0;
  padding: 0;
}
body {
  height: 100vh;

  overflow: hidden;
}

#shootingTestWrapper .container {
  width: 113.8889vh;
  height: 62.963vh;

  background: rgba(21, 22, 25, .95);

  border-radius: 2.2222vh;
  border: 0.1546vh solid rgba(234, 239, 243, 0.2);

  animation: arrive .2s forwards;
}

#shootingTestWrapper button {
  outline: none !important;
}

#shootingTestWrapper strong {
  color: white;
  font-family: "GothamX";
}

@keyframes arrive {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes counter {
  0% {
    opacity: 0;
    scale: .95;
  }
  50% {
    scale: 1.05;
  }
  100% {
    opacity: 1;
    scale: 1;
  }
}

button:hover {
  cursor: pointer;
}

@font-face {
  font-family: "Gotham";
  src: url(https://trigueirotech.com.br/fonts/Gotham-Font/GothamRegular.otf)
    format("OpenType");
}

